<script>
// @ is an alias to /src

export default {
  name: "AdicionarProduto",
  components: {},
  data() {
    return {
      produto: {
        nome: "",
        descricao: "",
        cod_sku: "",
        imagem: {},
        variacoes: []
      },

      form_variacao: {
        nome: "",
        tamanho: "",
        cor: "",
        quantidade: "",
        preco: "",
        sku: ""
      }
    };
  },
  methods: {
    AddVariacao() {
      this.produto.variacoes.push(this.form_variacao);
      this.form_variacao = {
        nome: "",
        tamanho: "",
        cor: "",
        quantidade: ""
      };
    },
    AddProduto(produto) {
      this.$store.commit("ADICIONAR_PRODUTO", produto);
    }
  }
};
</script>
