<template>
  <div class="container">
    <p class="titulo-pagina">Estoque</p >
<br>
  <div class="form-group " style="position:relative">
        <span class="">Buscar Produto:</span>
        <input type="text" id="input-buscar-produto" class="form-control" placeholder="Buscar Produto"  >
      </div>
      <br>
<table class="table table-hover">
  <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">Nome Produto</th>
      <th scope="col">Qts</th>
      <th scope="col">Imprimir</th>
      <th scope="col">Relatório</th>
      <th scope="col">Ver</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(produto, index) in Estoque" :key="index">
      <th scope="row">1</th>
      <td>{{produto.nome}}</td>
      <td>{{produto.variacoes.length}}</td>
      <td><span class="btn btn-sm btn-secondary">Imprimir Etiquetas</span></td>
      <td><span class="btn btn-sm btn-secondary" @click="Relatorio()">Relatório</span></td>
      <td><span class="btn btn-sm btn-secondary" @click="AbrirProduto(produto)">VER</span></td>
    </tr>
    
  </tbody>
</table>


  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Estoque',
  components: {
  },
  computed:{
    Estoque(){
      return this.$store.state.estoque
    }
  },
  methods:{
    AbrirProduto(produto){
       this.$store.commit("PRODUTO_ABERTO", produto);
               this.$router.push("detalhe-produto");
    },
    Relatorio(){
               this.$router.push("relatorio");

    }
  }
}
</script>
