import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Estoque from '../views/Estoque.vue'
import Usuarios from '../views/Usuarios.vue'
import AdicionarProduto from '../views/AdicionarProduto.vue'
import DetalheProduto from '../views/DetalheProduto.vue'
import QrCode from '../views/QrCode.vue'
import BaixaProduto from '../views/BaixaProduto.vue'
import Relatorio from '../views/Relatorio.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
    {
      path: '/estoque',
      name: 'Estoque',
      component: Estoque
    },
      {
        path: '/usuarios',
        name: 'Usuarios',
        component: Usuarios
      },
            {
              path: '/adicionar-produto',
              name: 'AdicionarProduto',
              component: AdicionarProduto
            },
            {
              path: '/detalhe-produto',
              name: 'DetalheProduto',
              component: DetalheProduto
            },
            {
              path: '/qr-code',
              name: 'QrCode',
              component: QrCode
            },
            {
              path: '/baixa-produto',
              name: 'BaixaProduto',
              component: BaixaProduto
            },
            {
              path: '/relatorio',
              name: 'Relatorio',
              component: Relatorio
            },
  
]

const router = new VueRouter({
  routes
})

export default router
